.App {
  min-height: 100vh;
  min-height: -webkit-fill-available;
  width: 100vw;
  margin: 0;
  font-family: "Mulish", sans-serif;
  box-sizing: border-box;
}

.fakeheader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 40px;
  box-sizing: border-box;
  margin: 0;
}

.logo {
  max-width: 90px;
}

.bottom {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background-image: linear-gradient(120deg, #002b82 30%, #ef4d3e 80%);
  height: -webkit-fill-available;
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: right;
  align-items: center;
  justify-content: center;
  color: white;
  z-index: -1;

  @media (min-width: 860px) {
    padding: 50px 40px;
    background-image: url("./assets/upscaled.png");
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.content {
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  margin: 0;
  padding: 50px 40px 0px 40px;
  @media (min-width: 860px) {
    padding: unset;
    align-items: flex-start;
    max-width: 400px;
    padding-top: 20px;
  }
}

.textbig {
  font-size: 30px;
  margin-bottom: 40px;
  /* font-size: 22px; */
  @media (min-width: 860px) {
    font-size: 36px;
  }
}

.textsmall {
  font-size: 20px;
  margin-bottom: 40px;
  /* font-size: 16px;
  @media (min-width: 860px) {
    font-size: 20px;
  } */
}

.actionwhite {
  background-color: white;
  color: black;
  margin-bottom: 40px;
  font-weight: 800;
  font-size: 20px;
  line-height: 20px;
  border-radius: 30px;
  padding: 20px 30px;
  cursor: pointer;
  border: none;
  outline: inherit;
}

.actionblack {
  background-color: black;
  color: white;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  border-radius: 24px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  outline: inherit;
}

.actionblack:hover {
  opacity: 0.5;
}

.actionwhite:hover {
  opacity: 0.5;
}

.phonediv {
  background-image: url("./assets/upscaled.png");
  background-position-x: right;
  background-size: cover;
  flex-grow: 3;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  @media (min-width: 860px) {
    background-image: none;
    justify-content: flex-end;
  }
}

.phoneImg {
  width: 35%;
  margin: 0;
}
